import React from 'react';
import "./sitepen.css";

function Sitepen(props) {
    const list=[{
        img:'',
        alt:'dojo framework logo',
        title:'Dojo Framework',
        content:'A modern, reactive TypeScript framework for building robust, enterprise-grade web applications.',
        button:'Learn More',
        order1:'order1',
        order2:'order2'
    },
    {
    img:'',
    alt:'intern logo',
    title:'Intern',
    order1:'',
    order2:'',
    content:'An all-in-one solution for full-lifecycle testing of JavaScript and TypeScript applications in Node and modern browsers.',
    button:'Learn More'
   },

   {
    img:'',
    alt:'dgrid logo',
    title:'dgrid',
    content:'An all-in-one solution for full-lifecycle testing of JavaScript and TypeScript applications in Node and modern browsers.',
    button:'Learn More',
    order1:'order1',
    order2:'order2'
   },
   {
    img:'',
    alt:'dojo toolkit logo',
    title:'Dojo Toolkit',
    order1:'',
    order2:'',
    content:'An all-in-one solution for full-lifecycle testing of JavaScript and TypeScript applications in Node and modern browsers.',
    button:'Learn More'
   },

   {
    img:'',
    alt:'Typescript logo',
    title:'TypeScript',
    content:'An all-in-one solution for full-lifecycle testing of JavaScript and TypeScript applications in Node and modern browsers.',
    button:'Learn More',
    order1:'order1',
    order2:'order2'
   },

    ]
    return (
        <>
            <section className="header">
              <div className='top-nav'>
                  <img src=" " title='home' alt="sitepen logo" />
                  <nav className='nav'>
                      <ul>
                      <li><a className="headies" href="#">what we do</a></li>
                      <li><a className="headies" href="#">Our work</a></li>
                      <li><a className="headies" href="#">Expertise</a></li>
                      <li><a className="headies" href="#">Insights</a></li>
                      <li><a className="headies" href="#">Contact</a></li>
                      </ul>
                  </nav>
              </div>

              <div className="open-source">
                    <h3> Commitment To</h3>
                    <h2> open source </h2>
                    <h2>software</h2>
                    <ul>
                         <li className="after">Collaboration</li>
                         <li className="after">Commitment</li>
                         <li>Community</li>
                    </ul>
              </div>

            </section>

            <section className="section2">
                <h3>Open Source Software Contributions</h3>
                  <div> We are lifelong advocates of open source software and have harnessed its benefits since the early days of JavaScript. We bring engineers from around the world together to collaborate, create and contribute so that better tools may continue to flourish — in the open. Here are some of the open source projects that we contribute to and support:</div>
            </section>

            <section className="section3">
                 {list.map(maps=>(
                     <>
                     <div className="row">

                     <div className={`imgs1 ${maps.order1}`}>
                            <img src={maps.img} alt={maps.alt} />
                        
                     </div>
                     
                     <div className={`maps ${maps.order2}`}>
                         <h3>{maps.title}</h3>
                         <p>{maps.content}</p>
                         <div className='button'>
                             <p>{maps.button}</p>
                         </div>
                     </div>

                     </div>
                     
                     </>
                 ))}
            </section>

            <section className="section4">
                  <h3>WE'D LOVE TO HEAR FROM YOU</h3>
                    <p className="p">Whether you need help with a current or upcoming project, initial advice and consultation, or just want to introduce yourself.</p>
                    <div>
                        <p className="blue">LET'S CONNECT </p>
                    </div>
            </section>
            <footer>
                    <div className="footer2">
                    <ul>
                            <li>About</li>
                                <li>Careers</li>
                                <li>open source</li>
                                <li>Ts Conf</li>
                                <li>talkscript.fm</li>
                                <li>milestone mayhem</li>
                                <li>contact</li>
                        </ul>

                        <div className="social">
                            <div>
                                <ul>
                                <li><a href="#">F</a></li>
                                <li><a href="#">T</a></li>
                                <li><a href="#">L</a></li>
                                <li><a href="#">S</a></li>
                                </ul>
                            </div>
                        </div>
                    </div>

             <div className="base">
                 <p>© 2021 SitePen, Inc. All Rights Reserved.</p>
                 <p>Privacy Policy</p>
             </div>
                
            </footer>
        </>
    );
}

export default Sitepen;